#title {
    text-align: center;
    color: #76BA99;
    margin-top: 40px;
    margin-bottom: 40px;
    font-weight: bold;
}

.all {
    display: flex;
    justify-content: center;
    align-items: center;
}

img {
    margin: 0;
}

.thumb {
    height: 280px;
    width: 300px;
    float: left;
}

#cart-form{
    /* align-items: center; */
    border-width: 1px;
    height: 280px;
    width: 400px;
    float: left;
    text-align: center;
    margin-bottom: 20px;
}

.label {
    font-weight: bold;
}

.closeButton {
    float: right;
}

.mb-3 {
    margin: 10px;
}

.caret {
    float: right;
    margin-right: 10px;
}

#btn {
    margin: 10px;
    background-color: #70AF85;
    border: none;
}

#modify {
    margin-left: 10px;
    background-color: #70AF85;
    border: none;
}

#order {
    margin-left: 10px;
    background-color: #AA8976;
    border: none;
}

#my-page{ display: block;
    justify-content: center;
    background-color: #70AF85;
    border: none;
}



@media all and (max-width: 768px){
    #title {
        font-size: 25px;
    }

    img {
        height: 100px;
        width: 100px;
    }
    /* .mb-3 {
        margin: 5px;
    } */

    #cart-form{
        /* align-items: center; */
        border-width: 1px;
        height: 280px;
        width: 100%;
        float: left;
        text-align: center;
        margin-bottom: 20px;
        margin-right: 0px;
        font-size: 10px;
    }

    .cart-small{
        margin: 0 auto;
        font-size: 10px;
    }

    #modify, #order {
        font-size: 10px;
    }

    .caret {
        float: right;
        margin-right: 10px;
        margin-top: 20px;
    }
}